.leftbar{
    background-color: black;
    width: 262px;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: fixed;
    overflow: hidden;
    z-index: 999;
}

@media screen and (max-width:1027px)and (min-width:845px){
    .leftbar{
        width: 195px;
    }
}
@media screen and (max-width:845px){
    .leftbar{
        width: 212px;
    }
}
@media screen and (max-width:770px){ 
    .leftbar{
        width: 200px;
    }
    .tags{
        font-size: 10px;
    }
}

.main{
    margin-left: 22px;
}
.logo img{
    margin-top: 18.5px;
    width: 136px;
    cursor: pointer;
}
.tag-container,.tag-container2{
    display: flex;
    flex-direction:row;
    align-items: center;
}
.tags{
    color: white;
    margin-top: 2vh;
}
.tag-container img{
    width: 26px;
    height: 25px;
    margin-right: 22px;
}
.tag-container p,.tag-container2 p{
    font-family: 'Ubuntu', sans-serif;
    font-size: 13.85px;
    text-decoration: none;
}
.heart{
    width: 15px;
    height: 15px;
}
.heart-container{
    
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    background-image: linear-gradient(rgb(40, 62, 158), rgb(143, 68, 179));
    margin-right: 25px;
}
.plus{
    width: 16px;
    height: 16px;
}
.plus-container{
    width: 24px;
    height: 24px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 25px;
    text-decoration: none;
}
.tag2container{
    margin-top: 2vh;
    text-decoration: none;
    width: 91%;
}
.hr{
    padding-bottom: 1.2vh;
    border-bottom: 0.1px solid rgb(75, 75, 75);
}
.tags a{
    text-decoration: none;
}